@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-family: 'Poppins', sans-serif; (main font)*/
/* font-family: 'Montserrat', sans-serif;(secondary font) */
/* font-family: 'Mulish', sans-serif;(third font) */


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --main-color: #4A00E0;
  --body-color:#FDF7FF;
  --dark-color:rgb(31, 25, 38);
  --secondary-color:#9C89B3;
  --gradient-color:#8E2DE2;
}

html{
  scroll-behavior: smooth;
}
.body{
  background-color: #FDF7FF;
  background-color: var(--body-color);
  overflow: hidden;
}

.menu{
position: fixed;
background-color: white;
top: 0;
z-index: 100;
}


.navbar{
  font-family:"Montserrat",sans-serif;
}

.text-home{
  color: rgb(31, 25, 38);
  color: var(--dark-color);
}

.text-home h1{
  font-family: "Poppins",sans-serif;
  font-size: 85px;
}
.text-home h2{
  font-family: "Poppins",sans-serif;
  font-size: 45px;
  font-weight: 500;
  line-height: 0.7;
  margin-bottom: 0;
}

.text-home p{
  color: #9C89B3;
  color: var(--secondary-color);
}

.text-home button{
  background-color: #4A00E0;
  background-color: var(--main-color);
  color: #FDF7FF;
  color: var(--body-color);
  box-shadow: 0px 7px 15px rgba(0, 0, 0, 0.3);
}



.custom-shape-divider-top-1624011552 .shape-fill {
  fill: #FFFFFF;
}

.image-home{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.custom-shape-divider-bottom {
  width: 100%;
  overflow: hidden;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  position: relative;
  top: 1px;
}
.custom-shape-divider-up {
  width: 100%;
  overflow: hidden;
  position: relative;
  bottom: 1px;
}

 
.custom-shape-divider-bottom,.custom-shape-divider-up .shape-fill {
  fill: #4A00E0;
  fill: var(--main-color);
}

.about{
  background-color: #4A00E0;
  background-color: var(--main-color);
  color: #FDF7FF;
  color: var(--body-color);
}
.about h1{
margin-bottom: 0;

}
.about p{
  margin-bottom: 0;
}
#about-intro{
  color: #9C89B3;
  color: var(--secondary-color);
}

.image-about{
  -webkit-align-items: center;
          align-items: center;
}

.image-about img{
  /* border-radius: 15px;
  box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.4) ; */
  width: 350px;
  -webkit-filter: drop-shadow(-6px 8px 10px rgba(0,0,0,0.5));
          filter: drop-shadow(-6px 8px 10px rgba(0,0,0,0.5));
}

.text-about{
  color: #FDF7FF ;
  text-align: right;
  font-family: 'Mulish', sans-serif;
}

.text-about button{
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  background-color: #FDF7FF;
  background-color: var(--body-color);
  color: #4A00E0;
  color: var(--main-color);
  box-shadow: 0px 7px 15px rgba(0, 0, 0, 0.3);
}

.about-outer{
  margin:48px auto ;
}

.mob-pad{
  padding: 48px;
}

.slidecontainer{
  width: 100%;
}
.slidecontainer input{
  width: 100%;
}


.Skills h1{
  margin-bottom: 30px;
}

.slidecontainer Slider{
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
}

.projectContainer{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.card{
  font-family: "Mulish",sans-serif;
  border-radius: 50px !important;
background: #fdf7ff;
box-shadow:  35px 35px 70px #d7d2d9,
             -35px -35px 70px #ffffff ;
border: none;
transition: 0.3s ease-in-out;
            }

.card:hover{
  transition: 0.3s ease-in-out;
  box-shadow: none;
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

button{
  font-family: "Mulish",sans-serif !important;
}

.qualification{
  background-color: #4A00E0;
  background-color: var(--main-color);
  color: #FDF7FF;
  color: var(--body-color);
}


.quali-text{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.quali p{
color: rgb(218, 218, 218);
}



/* footer */
.site-footer
{
  background-color:#26272b;
  padding:45px 0 20px;
  font-size:15px;
  line-height:24px;
  color:#737373;
}
.site-footer hr
{
  border-top-color:#bbb;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a
{
  color:#737373;
}
.site-footer a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none
}
.footer-links li
{
  display:block
}
.footer-links a
{
  color:#737373
}
.footer-links a:active,.footer-links a:focus,.footer-links a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
  width:40px;
  height:40px;
  line-height:40px;
  margin-left:6px;
  margin-right:0;
  border-radius:100%;
  background-color:#33353d
}
.copyright-text
{
  margin:0
}
@media (max-width:991px)
{
  .site-footer [class^=col-]
  {
    margin-bottom:30px
  }
}
@media (max-width:767px)
{
  .site-footer
  {
    padding-bottom:0
  }
  .site-footer .copyright-text,.site-footer .social-icons
  {
    text-align:center
  }
}
.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  margin-bottom:4px
}
.social-icons li.title
{
  margin-right:15px;
  text-transform:uppercase;
  color:#96a2b2;
  font-weight:700;
  font-size:13px
}
.social-icons a{
  background-color:#eceeef;
  color:#818a91;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  transition:all .2s linear
}
.social-icons a:active,.social-icons a:focus,.social-icons a:hover
{
  color:#fff;
  background-color:#29aafe
}
.social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}
.social-icons a.facebook:hover
{
  background-color:#3b5998
}
.social-icons a.twitter:hover
{
  background-color:#00aced
}
.social-icons a.linkedin:hover
{
  background-color:#007bb6
}
.social-icons a.dribble:hover
{
  background-color:#ea4c89
}
@media (max-width:767px)
{
  .social-icons li.title
  {
    display:block;
    margin-right:0;
    font-weight:600
  }
}







.mont-font{
  font-family: "Montserrat", sans-serif;
}
.muli-font{
  font-family: "Mulish", sans-serif;
}
.pop-font{
  font-family: "Poppins",sans-serif;
}


















/* tablets */
@media(max-width:770px){
  .text-home{
    text-align: center;
  }
  .text-home h1{
    font-size: 65px;
    line-height: 1;
  }
  .text-home h2{
    font-size:25px;
  }
  .image-home{
    -webkit-justify-content: center;
            justify-content: center;
  }
  .text-about{
    text-align: center;
    color: #FDF7FF;
    color: var(--body-color);
  }
  .mob-pad{
    padding:0 !important;
    margin: 0;
  }

  .about-outer{
    margin: 0 auto 24px;
  }
  .image-about img{
    width: 250px;
  }
.quali-text{
text-align: center;
}
}


/* mobiles */
@media(max-width:380px){
  .text-home h1{
    font-size: 50px;
    margin-top: 10px;
  }
  .image-about img{
    width: 175px;
  }

}
